.homePageTitle {
  font-family: 'Zilla Slab Highlight', cursive;
  font-size: 550%;
  display: flex;
  justify-content: center;
}

.slogan {
  font-size: 160%;
  font-family: 'Dancing Script', cursive;
}

.img {
  width: 4%;
}

.description {
  font-size: 110%;
  font-family: 'Hepta Slab', serif;
}

.welcomePage {
  text-align: center;
  font-family: 'Righteous', cursive;
  font-size: 160%;
  color: white;
  
 -webkit-animation: colorchange 30s infinite; 
    animation: colorchange 30s infinite;
}

@-webkit-keyframes colorchange {
    0%  {
       background-image:-webkit-gradient(linear, left top, right top, from(#F8B195), color-stop(#F67280), color-stop(#C06C84),  color-stop(#6C5B7B), to(#355C7D) );
       background-image:-webkit-linear-gradient(left, #F8B195, #F67280, #C06C84,  #6C5B7B, #355C7D );
       background-image:linear-gradient(to right, #F8B195, #F67280, #C06C84,  #6C5B7B, #355C7D );
     }
    25%  {
       background-image:-webkit-gradient(linear, left top, right top, from(#205072), color-stop(#329D96) , color-stop(#56C596), color-stop(#7BE495), to(#CFF4D2) );
       background-image:-webkit-linear-gradient(left, #205072, #329D96 , #56C596, #7BE495, #CFF4D2 );
       background-image:linear-gradient(to right, #205072, #329D96 , #56C596, #7BE495, #CFF4D2 )
    }
    50% {
      background-image:-webkit-gradient(linear, left top, right top, from(#cc6600), to(#ff6699) );
      background-image:-webkit-linear-gradient(left, #cc6600, #ff6699 );
      background-image:linear-gradient(to right, #cc6600, #ff6699 )
    }
    75%  {
      background-image:-webkit-gradient(linear, left top, right top, from(#FE4365), color-stop(#FC9D9A) , color-stop(#F9CDAD) , color-stop(#C8C8A9), to(#83AF9B) );
      background-image:-webkit-linear-gradient(left, #FE4365, #FC9D9A , #F9CDAD , #C8C8A9, #83AF9B );
      background-image:linear-gradient(to right, #FE4365, #FC9D9A , #F9CDAD , #C8C8A9, #83AF9B )
    }
    100% {
      background-image:-webkit-gradient(linear, left top, right top, from(#003399), to(#33cc33)  );
      background-image:-webkit-linear-gradient(left, #003399, #33cc33  );
      background-image:linear-gradient(to right, #003399, #33cc33  )
    }
}

@keyframes colorchange {
    0%  {
       background-image:-webkit-gradient(linear, left top, right top, from(#F8B195), color-stop(#F67280), color-stop(#C06C84),  color-stop(#6C5B7B), to(#355C7D) );
       background-image:-webkit-linear-gradient(left, #F8B195, #F67280, #C06C84,  #6C5B7B, #355C7D );
       background-image:linear-gradient(to right, #F8B195, #F67280, #C06C84,  #6C5B7B, #355C7D );
     }
    25%  {
       background-image:-webkit-gradient(linear, left top, right top, from(#205072), color-stop(#329D96) , color-stop(#56C596), color-stop(#7BE495), to(#CFF4D2) );
       background-image:-webkit-linear-gradient(left, #205072, #329D96 , #56C596, #7BE495, #CFF4D2 );
       background-image:linear-gradient(to right, #205072, #329D96 , #56C596, #7BE495, #CFF4D2 )
    }
    50% {
      background-image:-webkit-gradient(linear, left top, right top, from(#cc6600), to(#ff6699) );
      background-image:-webkit-linear-gradient(left, #cc6600, #ff6699 );
      background-image:linear-gradient(to right, #cc6600, #ff6699 )
    }
    75%  {
      background-image:-webkit-gradient(linear, left top, right top, from(#FE4365), color-stop(#FC9D9A) , color-stop(#F9CDAD) , color-stop(#C8C8A9), to(#83AF9B) );
      background-image:-webkit-linear-gradient(left, #FE4365, #FC9D9A , #F9CDAD , #C8C8A9, #83AF9B );
      background-image:linear-gradient(to right, #FE4365, #FC9D9A , #F9CDAD , #C8C8A9, #83AF9B )
    }
    100% {
      background-image:-webkit-gradient(linear, left top, right top, from(#003399), to(#33cc33)  );
      background-image:-webkit-linear-gradient(left, #003399, #33cc33  );
      background-image:linear-gradient(to right, #003399, #33cc33  )
    }
} 

