  font-size: 100%;
  color: white;
}

.loginButton {
  position: absolute;
  top: 50%;
  left: 95%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: #f1f1f1;
  color: black;
  font-size: 13px;
  padding: 16px 30px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.loginButton:hover{
  background-color: black;
  color: white;
}

.input {
  color: white;
}

.addButton {
  text-align: center;
  background-color:white;
  color: black;
  cursor: pointer;
  padding: 10px 5px;
}

.addButton:hover{
  background-color: black;
  color: white;
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}

.myProjects {
  font-size: 120%;
